body {
  max-width: 100%;
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

.wrapper-select {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.ss-main {
  width: 50%;
}

.cat-info {
  width: 800px;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px 30px 30px;
  display: flex;
}

.error {
  width: 100%;
  text-align: center;
}

.is-hidden {
  display: none;
}

.loader {
  letter-spacing: 2px;
  color: #ff3d00;
  text-shadow: 0 0 1px #000;
  margin-top: 100px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 72px;
  font-weight: bold;
  animation: smokeOut .75s ease-in-out infinite alternate;
}

.loader:before {
  content: "Loading";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

